import { useMutation } from '@tanstack/react-query'
import React, { useState } from 'react'
import GenderSelect from '../../../../../../components/common/Select/GenderSelect'
import ExistBox from '../../../../../../components/layout/ExistBox'
import WhiteBtnSM from '../../../../../../components/UI/Atoms/Button/WhiteBtnSM'
import DatepickerSM from '../../../../../../components/UI/Atoms/Datepicker/DatepickerSM'
import InfoLabelSM from '../../../../../../components/UI/Atoms/Info/InfoLabelSM'
import InfoEditInput from '../../../../../../components/UI/Atoms/Input/InfoEditInput'
import { useDebug } from '../../../../../../hooks/basic/useDebug'
import { useInput } from '../../../../../../hooks/input/useInput'
import { useInputs } from '../../../../../../hooks/input/useInputs'

import { Grid } from '../../../../../../components/common/Grid/Grid'
import ProfileImage from '../../../../../../components/UI/Atoms/Profile/ProfileImage'
import { getExistStudentByCode, getExistStudentByInfo, selectExistStudent } from '../../../../../../api/Student/studentApi'
import { getFullname } from '../../../../../../utils/render/getFullname'
import { useRecoilValue } from 'recoil'
import dojangIdAtom from '../../../../../../recoil/atom/dojangIdAtom'


const ExistStudent = () => {
  const dojangId = useRecoilValue(dojangIdAtom)

  // state
  const [ studentList, setStudentList ] = useState('')
  const [ showGrid, setShowGrid ] = useState(false)

  // custom hook
  const [ type, handleRadio ] = useInput('1')
  const [ info, setInfo, handleChange] = useInputs('')
  const [ code, handleCode ] = useInput('')

  // query
  const { mutate: infoMT } = useMutation((info) => getExistStudentByInfo(info))
  const { mutate: codeMT } = useMutation((code) => getExistStudentByCode(code))
  const { mutate: resistMT, isLoading } = useMutation(([studentId, body]) => selectExistStudent([studentId, body]))

  //handler
  const handleSelect = () => {
    switch (type) {
      case '1' : 
        infoMT(info, {
          onSuccess: (res) => {
            setStudentList(res)
            setShowGrid(true)
          }
        })
        return
      case '2' :
        codeMT(code, {
          onSuccess: (res) => {
            setStudentList(res)
            setShowGrid(true)
          }
        })
        return
      default: return alert('오류가 발생하였습니다.')
    }
  }
  // useDebug(studentList)

  const handleClick = (e) => {
    let studentId = e.target.id
    let body = { dojang: dojangId }

    resistMT([studentId, body], {
      onSuccess: () => {
        alert('성공적으로 등록 되었습니다.')
        window.location.reload()
      }
    })
  }


  return (
    <>
      <ExistBox bg={'#555555'}>
        <div className="flex gap-2">
          <input type='radio' name='type' value='1' onChange={handleRadio} checked={type==='1'}/>
          <span>기본 정보로 조회</span>
          <input type='radio' name='type' value='2' onChange={handleRadio} checked={type==='2'}/>
          <span>코드로 조회</span>
        </div>
      </ExistBox>
      { type === '1' && 
        <ExistBox>
          <div className='flex justify-between items-center gap-[10px]'>
            <div className='flex items-center flex-1'>
              <InfoLabelSM>이름</InfoLabelSM>
              <InfoEditInput length='62px' name='last_name' value={info?.last_name || ''} onChange={handleChange} placeholder='성' className='mr-[2px]' />
              <InfoEditInput length='124px' name='first_name' value={info?.first_name || ''} onChange={handleChange} placeholder='이름' />
            </div>
            <div className='flex items-center flex-1'>
              <InfoLabelSM>생년월일</InfoLabelSM>
              <DatepickerSM length={186} name='dob' value={info?.dob || ''} onChange={handleChange} /> 
            </div>
            <div className='flex items-center flex-1'>
              <InfoLabelSM>성별</InfoLabelSM>
              <GenderSelect name='sex' value={info?.sex || ''} onChange={handleChange} className='text-[14px]' />
            </div>
          </div>
        </ExistBox>
      }
      { type === '2' && 
        <ExistBox>
          <div className='flex justify-between items-center'>
            <div className='flex items-center gap-[10px]'>
              <InfoLabelSM>코드</InfoLabelSM>
              <InfoEditInput value={code} onChange={handleCode} />
            </div>
          </div>
        </ExistBox>
      }
      <div className='flex justify-center my-[20px]'>
        <WhiteBtnSM onClick={handleSelect}>조회</WhiteBtnSM>
      </div>
      {/* <ExistGrid /> */}
      {showGrid && 
        <Grid exist>
          <Grid.RowWrap>
            { studentList?.length === 0 && 
              <div className="w-full h-full flex items-center justify-center text-[#777777]">조회 결과가 없습니다.</div>
            }
            { React.Children.toArray(
              studentList?.map(item =>
                <Grid.Row>
                  <Grid.Col w={0.5}><ProfileImage urlKey={item?.photo_url} /></Grid.Col>
                  <Grid.Col w={1}>{getFullname(item?.last_name, item?.first_name)}</Grid.Col>
                  <Grid.Col w={1}>{item?.level}</Grid.Col>
                  <Grid.Col w={1}>{item?.sex}</Grid.Col>
                  <Grid.Col w={1}>{item?.dob}</Grid.Col>
                  <Grid.Col w={0.5}><WhiteBtnSM id={item?.id} onClick={handleClick} disabled={isLoading}>선택</WhiteBtnSM></Grid.Col>
                </Grid.Row>
              )
            )}
          </Grid.RowWrap>
        </Grid>
      }
    </>
  )
}

export default ExistStudent