import React from 'react';
import { useRecoilState } from 'recoil';
import Box from '../../../../../../../components/layout/Box';
import DatepickerSM from '../../../../../../../components/UI/Atoms/Datepicker/DatepickerSM';
import InfoLabelSM from '../../../../../../../components/UI/Atoms/Info/InfoLabelSM';
import { registDataAtom } from '../../../recoil/atom/registDataAtom';
import RegInfo from './RegInfo';
import BasicTextArea from '../../../../../../../components/UI/Atoms/Textarea/BasicTextArea';
import AdornmentInput from '../../../../../../../components/UI/Atoms/Input/AdornmentInput';
import { numberMaxLength2 } from '../../../../../../../utils/render/numberMaxLength';

const StepE = () => {
  const [reg, setReg] = useRecoilState(registDataAtom);

  // handler
  const handleChange = (e) => {
    const { name, value } = e.target;
    setReg({ ...reg, [name]: value });
  };

  return (
    <>
      <RegInfo />
      <Box>
        <div className="flex flex-col gap-2 px-[86px]">
          <div className="flex justify-between items-center gap-[40px]">
            <div className="flex flex-1 items-center">
              <InfoLabelSM>도장 등록일*</InfoLabelSM>
              <DatepickerSM
                length={186}
                name="dojang_reg_date"
                value={reg.dojang_reg_date}
                onChange={handleChange}
              />
            </div>
            <div className="flex flex-1"></div>
          </div>
          <div className="flex justify-between items-center gap-[40px]">
            <div className="flex flex-1 items-center">
              <InfoLabelSM>줄넘기 급수</InfoLabelSM>
              <AdornmentInput
                unit="급"
                placeholder="0"
                type="number"
                name="jump_level"
                value={reg.jump_level}
                onChange={handleChange}
                onInput={numberMaxLength2}
              />
            </div>
            <div className="flex flex-1 items-center">
              <InfoLabelSM>줄넘기 경력</InfoLabelSM>
              <AdornmentInput
                unit="년"
                placeholder="0"
                type="number"
                name="jump_career"
                value={reg.jump_career}
                onChange={handleChange}
                onInput={numberMaxLength2}
              />
            </div>
          </div>
          <div className="flex flex-1 items-start mb-[20px]">
            <InfoLabelSM>메모</InfoLabelSM>
            <BasicTextArea
              placeholder="기타 수련생의 참고 사항을 적어주세요."
              length={520}
              rows={3}
              name="memo"
              value={reg.memo}
              onChange={handleChange}
            />
          </div>
        </div>
      </Box>
    </>
  );
};

export default StepE;
