import React, { useState } from 'react'
import StudentList from './component/StudentList'
import { useSelect } from '../../../../hooks/basic/useSelect';
import { useStudent } from './hooks/queries/useStudent'
import PageContainer from '../../../../components/layout/PageContainer';
import ModalContainer from '../../../../components/layout/ModalContainer';
import RegistStudent from './component/RegistStudent';
import ClassSelectMD from '../../../../components/common/Class/ClassSelectMD';
import { useResetRecoilState } from 'recoil';
import { registDataAtom } from './recoil/atom/registDataAtom';
import { useDebug } from '../../../../hooks/basic/useDebug';
import CreateBtnMD from '../../../../components/UI/Atoms/Button/CreateBtnMD';


const ManageStudentPage = () => {
  const [ open, setOpen ] = useState(false)

  // recoil
  const resetReg = useResetRecoilState(registDataAtom)

  // custom hook
  const [classes, handleChangeClasses] = useSelect('0')

  // query
  const { data: students, refetch } = useStudent(classes)

  // handler
  const handleClickOpenModal = () => {
    setOpen(true)
  }

  const handleClickCloseModal = () => {
    setOpen(false)
    resetReg()
  }

  return (
    <>
    <PageContainer width="md">
      <div className='flex justify-between items-center mb-3 min-w-[900px] mx-auto'>
        <div className="flex gap-[10px]">
          <ClassSelectMD value={classes} onChange={handleChangeClasses} />
        </div>
        <div>
          <CreateBtnMD onClick={handleClickOpenModal}>+ 수련생 등록</CreateBtnMD>
        </div>
      </div>
      {students && <StudentList res={students} refetch={refetch}/>}
    </PageContainer>

    {/* modal */}
    <ModalContainer
      open={open}
      onClose={handleClickCloseModal}
      title='수련생 등록'
    >
      <RegistStudent />
    </ModalContainer>
    </>
  )
}
      

export default ManageStudentPage