import React from 'react'
import { useRecoilValue } from 'recoil'
import Box from '../../../../../../../components/layout/Box'
import InfoLabelSM from '../../../../../../../components/UI/Atoms/Info/InfoLabelSM'
import ProfileImage from '../../../../../../../components/UI/Atoms/Profile/ProfileImage'
import { registDataAtom } from '../../../recoil/atom/registDataAtom'
import RegInfoTable from '../../../../../../../components/UI/Molecules/Table/RegInfoTable'
import { toGender } from '../../../../../../../utils/render/toGender'

const RegInfo = () => {
  const reg = useRecoilValue(registDataAtom)

  return (
    <Box>
      <div className="flex gap-[50px] justify-center">
        <ProfileImage 
          size='140px'
          previewUrl={reg?.previewUrl}
        />
        <table>
          <RegInfoTable label="이름">{reg.last_name + reg.first_name}</RegInfoTable>
          <RegInfoTable label="생년월일">{reg.dob}</RegInfoTable>
          <RegInfoTable label="성별">{toGender(reg.sex)}</RegInfoTable>
        </table>
      </div>
    </Box>
  )
}

export default RegInfo