import React, { useEffect, useState } from 'react';
import { styled } from '@mui/system';
import { Box } from '@mui/material';

const BoxStyle = styled(Box)(({ theme }) => ({
  width: '30px',
  height: '30px',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  backgroundColor: theme.palette.background.default,
  borderRadius: '50%',
  cursor: 'pointer',
  transition: 'all 0.3s ease-in-out',

  '&:hover': {
    opacity: '0.5',
  },
}));

const CloseIcons = ({ src, ...props }) => {

  const close_B_icon = 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/close_B_vector.png'
  const close_W_icon = 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/close_W_vector.png'

  const [iconSrc, setIconSrc] = useState('')

  useEffect(() => {
    if (src === 'close_B') {
      setIconSrc(close_B_icon)
    } else if (src === 'close_W') {
      setIconSrc(close_W_icon)
    }
  }, [src])

  return (
    <>
      <BoxStyle {...props}>
        <img src={iconSrc} alt="close_icon" />
      </BoxStyle>
    </>
  );

};

export default CloseIcons;
