import React from 'react'
import InfoLabelMD from '../../Atoms/Info/InfoLabelMD';

const RegInfoTable = ({label, children }) => {
  return (
    <>
      <tbody>
        <tr>
          <td className="w-[88px]"><InfoLabelMD className='text-[17px]'>{label}</InfoLabelMD></td>
          <td className="px-[20px]">{children}</td>
        </tr>
      </tbody>
    </>
  )

    // 사용할때 <table>태그로 감싸서 사용해야함
}

export default RegInfoTable;