import { Typography } from '@mui/material'
import React, { useRef } from 'react'
import { useRecoilState } from 'recoil'
import GenderSelect from '../../../../../../../components/common/Select/GenderSelect'
import GenderSelectSM from '../../../../../../../components/common/Select/GenderSelectSM'
import Box from '../../../../../../../components/layout/Box'
import DatepickerSM from '../../../../../../../components/UI/Atoms/Datepicker/DatepickerSM'
import InfoEditInput from '../../../../../../../components/UI/Atoms/Input/InfoEditInput'
import InfoLabelSM from '../../../../../../../components/UI/Atoms/Info/InfoLabelSM'
import ProfileImage from '../../../../../../../components/UI/Atoms/Profile/ProfileImage'
import ClassSelectSM from '../../../../../../../components/common/Class/ClassSelectSM'
import { useFileInput } from '../../../../../../../hooks/file/useFileInput'
import { registDataAtom } from '../../../recoil/atom/registDataAtom'
import WhiteBtnMD from '../../../../../../../components/UI/Atoms/Button/WhiteBtnMD'
import LoadingModal from '../../../../../../../components/UI/Atoms/Spinner/LoadingModal'

const StepA = () => {
  const [ reg, setReg ] = useRecoilState(registDataAtom)


  // custom Hook
  const [ handleImgInput, fileInputRef, clickUpload ] = useFileInput({
    data: reg,
    set: setReg,
    urlKey: 'previewUrl',
    fileKey: 'img',
    fileSize: 500,
  })

  // handler
  const handleChange = (e) => {
    const { name, value } = e.target
    setReg({...reg, [name]:value})
  }

  // useDebug(reg)
  

  return (
    <>
      <div className="flex justify-center">
        <div className="flex flex-col gap-[20px] items-center">
          <ProfileImage
            full
            size="140px"
            previewUrl={reg?.previewUrl}
            onChange={handleImgInput}
            ref={fileInputRef}
          />
          <WhiteBtnMD onClick={clickUpload} className="w-[140px]">
            + 사진 선택*
          </WhiteBtnMD>
          <Typography variant="h5" className="text-center mb-[20px] leading-6 tracking-normal">
            정면을 보고 있는 사진으로 등록해주세요. <br />
            승급증 또는 품/단증에 들어가는 공식 사진입니다.
          </Typography>
        </div>
      </div>
      <Box>
        <div className="px-[60px] py-[6px]">
          <div className="flex gap-[50px] mb-[20px]">
            <div className="flex items-center flex-1">
              <InfoLabelSM className="w-[88px]">이름*</InfoLabelSM>
              <div className="flex w-[186px] gap-[2px]">
                <InfoEditInput
                  length="83px"
                  name="last_name"
                  value={reg?.last_name}
                  onChange={handleChange}
                  placeholder="성"
                  inputProps={{ maxLength: 3 }}
                />
                <InfoEditInput
                  length="124px"
                  name="first_name"
                  value={reg?.first_name}
                  onChange={handleChange}
                  placeholder="이름"
                  inputProps={{ maxLength: 6 }}
                />
              </div>
            </div>
            <div className="flex items-center flex-1">
              <InfoLabelSM>성별*</InfoLabelSM>
              <GenderSelect name="sex" value={reg?.sex} onChange={handleChange} className='text-[14px]' />
            </div>
          </div>
          <div className="flex gap-[50px] mb-[20px]">
            <div className="flex items-center flex-1">
              <InfoLabelSM>생년월일*</InfoLabelSM>
              <DatepickerSM length={186} name="dob" value={reg?.dob} onChange={handleChange} />
            </div>
            <div className="flex items-center flex-1">
              <InfoLabelSM>소속반*</InfoLabelSM>
              <ClassSelectSM
                className="text-[14px]"
                name="class_id"
                value={reg?.class_id}
                onChange={handleChange}
              />
            </div>
          </div>
        </div>
      </Box>
    </>
  );
}

export default StepA