import React, { useState } from 'react'
import { useRecoilValue } from 'recoil'
import MenuItem from '@mui/material/MenuItem';
import { useQuery } from '@tanstack/react-query';
import { getClassList } from '../../../api/Class/ClassApi';
import { Select } from '@mui/material';
import dojangIdAtom from '../../../recoil/atom/dojangIdAtom';


const selectItemStyle = {
  width: '100%',
  position: 'relative',
  display: 'flex',
  alignItems: 'center',
  '&:hover': {
    backgroundColor: '#f3f3f3',
    fontWeight: 'bold',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
  '&:focus': {
    fontWeight: 'bold',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
}

const ClassSelectSM = ({ value, onChange, ...props }) => {

  const dojangId = useRecoilValue(dojangIdAtom)

  // query
  const { data } = useQuery(['getClassList', dojangId], () => getClassList(dojangId), {
    enabled: !!dojangId
  })
  return (
    <>
      <Select 
        sx={{width: '186px', height: '30px'}}
        size='small'
        placeholder='반 선택'
        value={value}
        onChange={onChange}
        {...props}
      >
        { data && React.Children.toArray( 
          data?.rows?.map(({id, title}) =>
            <MenuItem value={id} sx={selectItemStyle}
            >
              {title}
            </MenuItem>
          )
        )}
      </Select>
    </>
  )
}

export default ClassSelectSM