import React, { useState, useEffect } from 'react';
import Box from '../../../../../../components/layout/Box';
import { useDebug } from '../../../../../../hooks/basic/useDebug';
import EditBtn from '../../../../../../components/UI/Atoms/Button/EditBtnSM';
import WhiteBtn from '../../../../../../components/UI/Atoms/Button/WhiteBtnSM';
import BlackBtn from '../../../../../../components/UI/Atoms/Button/BlackBtnSM';
import ProfileImage from '../../../../../../components/UI/Atoms/Profile/ProfileImage';
import { Input, Label, Col, Row } from './StudentForm/StudentForm';
import { useFile } from '../../../../../../hooks/file/useFile';
import { useModal } from '../../../../../../hooks/basic/useModal';
import { getFormData } from '../../../../../../utils/basic/getFormData';
import { useStudentSpecInfo } from '../../hooks/queries/useStudentSpecInfo';
import { useUpdateStudentSpecInfo } from '../../hooks/queries/useUpdateStudentSpecInfo';
import { useInputs } from '../../../../../../hooks/input/useInputs';
import ProfileEditUploadBtn from '../../../../../../components/UI/Atoms/Icon/ProfileEditUploadBtn';
import DatepickerSM from '../../../../../../components/UI/Atoms/Datepicker/DatepickerSM';
import { numberWithCommas, phoneHyphenRegex } from '../../../../../../utils/lib/Regex';
import BasicTextArea from '../../../../../../components/UI/Atoms/Textarea/BasicTextArea';
import AdornmentInput from '../../../../../../components/UI/Atoms/Input/AdornmentInput';
import { toGender } from '../../../../../../utils/render/toGender';
import GenderSelect from '../../../../../../components/common/Select/GenderSelect';
import GenderSelectSM from '../../../../../../components/common/Select/GenderSelectSM';
import AccountPopDetail from '../../../../../../components/UI/Molecules/Account/AccountPopDetail';
import { getFullname } from '../../../../../../utils/render/getFullname';
import { useMutation } from '@tanstack/react-query';
import { getStudentSpecInfo } from '../../../../../../api/Student/studentApi';
import ModalAddress from '../../../../../../components/common/PostCode/ModalAddress';
import {
  numberMaxLength,
  numberMaxLength1,
  numberMaxLength2,
} from '../../../../../../utils/render/numberMaxLength';
import PayMethodSelect from '../../../../../../components/UI/Atoms/Select/PayMethodSelect';
import { nameRegex, lastNameRegex } from '../../../../../../utils/lib/Regex';
import { useFileInput } from '../../../../../../hooks/file/useFileInput';
import { formDataDebug } from '../../../../../../utils/debug/debug';
import RankImage from '../../../../../../components/common/Rank/RankImage';
import { toLevelString } from '../../../../../../utils/basic/transform';

const SpecInfo = ({ info, setInfo }) => {
  // { last_name, first_name, sex, dob, school, phone_number, email, address//, address_detail //기본정보
  // , Rep_rel, Rep_name, Rep_phone_number, ParentsInfos //가족정보
  // , career, level, belt, level_number //태권정보
  // , due_date, paying_amount, payment_method, depositor_name, payment_note //납부정보
  // , jump_career, jump_level //줄넘기정보
  // , memo }

  // state
  const [edit, setEdit] = useState(true);
  const [editData, setEditData, handleChange] = useInputs({ ...info });

  // query
  const { mutate } = useMutation((studentId) => getStudentSpecInfo(studentId));
  const updateStudentSpecInfo = useUpdateStudentSpecInfo({
    option: {
      onSuccess: () => {
        mutate(info?.id, {
          onSuccess: (res) => {
            setInfo(res);
            setEditData(res);
          },
        });
      },
      onError: (err) => {
        alert(err);
        setEditData({ ...info });
      },
    },
  });

  // custom hook (modal)
  const [open, openFn, closeFn] = useModal();

  // custom Hook
  const [handleFileInput, fileInputRef, clickUpload] = useFileInput({
    data: editData,
    urlKey: 'previewUrl',
    fileKey: 'img',
    set: setEditData,
  });

  const postData = () => {
    const { photo_url, previewUrl, ParentsInfos, DojangInfos, classInfos, img, ...postData } =
      editData;
    const data = [
      ...Object.entries(postData?.user == null ? { ...postData, user: '-1' } : postData),
    ];
    if (img) {
      data.push(['img', img]);
    }

    if (!nameRegex.test(postData?.first_name)) {
      alert('이름은 한글로 1~6까지 입력 가능합니다.');
      return;
    }
    if (!lastNameRegex.test(postData?.last_name)) {
      alert('성은 한글로 1~3까지 입력 가능합니다.');
      return;
    }

    const formData = getFormData(data);
    // formDataDebug(formData)

    updateStudentSpecInfo.mutate([info?.id, formData]);
    alert('저장되었습니다.');
    setEdit(true);
  };

  // handler
  const handleCancle = () => {
    setEditData({ ...info });
    setEdit(true);
  };
  const handleSave = () => {
    postData();
  };

  // useDebug(editData);
  return (
    <>
      <Box
        title="기본 정보"
        btn={
          !edit ? (
            <div className="flex gap-[3px]">
              <WhiteBtn onClick={handleCancle}>취소</WhiteBtn>
              <BlackBtn onClick={handleSave}>저장</BlackBtn>
            </div>
          ) : (
            <div className="flex gap-2">
              <EditBtn onClick={() => setEdit(false)}>수정</EditBtn>
            </div>
          )
        }
      >
        <div className="flex items-center gap-[20px]">
          <div className="flex flex-col items-center relative">
            <ProfileImage
              size="140px"
              urlKey={editData?.photo_url || ''}
              previewUrl={editData?.previewUrl || ''}
              onChange={handleFileInput}
              disabled={edit}
              ref={fileInputRef || ''}
            />
            {!edit && <ProfileEditUploadBtn onClick={clickUpload}></ProfileEditUploadBtn>}
          </div>
          <div className="flex flex-col">
            <Row>
              <Col col={1}>
                <Label>이름</Label>
                <div className="flex w-[186px]">
                  {edit ? (
                    <Input
                      placeholder="이름 입력"
                      value={getFullname(editData?.last_name, editData?.first_name || '')}
                      disabled={edit}
                      inputProps={{ maxLength: 9 }}
                    />
                  ) : (
                    <>
                      <Input
                        placeholder="성"
                        name="last_name"
                        value={editData?.last_name || ''}
                        onChange={handleChange}
                        disabled={edit}
                        inputProps={{ maxLength: 3 }}
                      />
                      <Input
                        placeholder="이름"
                        name="first_name"
                        value={editData?.first_name || ''}
                        onChange={handleChange}
                        disabled={edit}
                        inputProps={{ maxLength: 6 }}
                      />
                    </>
                  )}
                </div>
              </Col>
              <Col col={1} className="justify-end">
                <Label>성별</Label>
                {edit ? (
                  <Input
                    name="sex"
                    value={toGender(editData?.sex)}
                    onChange={handleChange}
                    disabled={edit}
                  />
                ) : (
                  <GenderSelectSM
                    placeholder={!edit ? '성별 선택' : ''}
                    name="sex"
                    value={editData?.sex}
                    onChange={handleChange}
                    className="text-[14px] pt-[2px] rounded-[2px] w-[186px] h-[30px]"
                  />
                )}
              </Col>
            </Row>
            <Row>
              <Col>
                <Label>체중</Label>
                <AdornmentInput
                  unit="kg"
                  name="weight"
                  type="number"
                  placeholder={!edit ? '체중 입력' : ''}
                  value={editData?.weight || ''}
                  onChange={handleChange}
                  disabled={edit}
                  onInput={numberMaxLength}
                />
              </Col>
              <Col>
                <Label>키</Label>
                <AdornmentInput
                  unit="cm"
                  name="height"
                  type="number"
                  placeholder={!edit ? '키 입력' : ''}
                  value={editData?.height || ''}
                  onChange={handleChange}
                  disabled={edit}
                  onInput={numberMaxLength}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <Label>생년월일</Label>
                {edit ? (
                  <Input
                    name="dob"
                    value={editData?.dob || ''}
                    onChange={handleChange}
                    disabled={edit}
                  />
                ) : (
                  <DatepickerSM
                    name="dob"
                    length={186}
                    value={editData?.dob || ''}
                    onChange={handleChange}
                  />
                )}
              </Col>
              <Col>
                <Label>학교</Label>
                <Input
                  name="school"
                  placeholder={!edit ? '학교명 입력' : ''}
                  value={editData?.school || ''}
                  onChange={handleChange}
                  disabled={edit}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <Label>전화번호</Label>
                <Input
                  name="phone_number"
                  value={editData?.phone_number || ''}
                  onChange={handleChange}
                  disabled={edit}
                />
              </Col>
              <Col>
                <Label>메일</Label>
                <Input
                  name="email"
                  type="email"
                  placeholder={!edit ? '메일 입력' : ''}
                  value={editData?.email || ''}
                  onChange={handleChange}
                  disabled={edit}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <Label>주소</Label>
                <div className="flex justify-between w-[525px]">
                  {edit && (
                    <Input
                      name="address_name"
                      placeholder={!edit ? '주소 검색' : ''}
                      value={editData?.address_name || ''}
                      onChange={handleChange}
                      disabled={edit}
                      className="min-w-[335px]"
                    />
                  )}
                  {!edit && (
                    <Input
                      name="address_name"
                      placeholder={!edit ? '주소 검색' : ''}
                      value={editData?.address_name || ''}
                      // onChange={handleChange}
                      disabled={edit}
                      className="min-w-[335px]"
                      onClick={() => openFn()}
                    />
                  )}
                  {!edit && <ModalAddress open={open} onClose={closeFn} setValue={setEditData} />}
                  <Input
                    name="address_detail"
                    placeholder={!edit ? '상세 주소' : ''}
                    value={editData?.address_detail || ''}
                    onChange={handleChange}
                    disabled={edit}
                  />
                </div>
              </Col>
            </Row>
          </div>
        </div>
      </Box>
      <Box title="가족 정보">
        <div className="flex flex-col gap-2">
          <Row>
            <Col col={1}>
              <Label>보호자 이름</Label>
              <Input
                name="Rep_name"
                value={editData?.Rep_name || ''}
                onChange={handleChange}
                disabled={edit}
                inputProps={{ maxLength: 10 }}
              />
            </Col>
            <Col col={1}>
              <Label>보호자 연락처</Label>
              <Input
                name="Rep_phone_number"
                value={editData?.Rep_phone_number || ''}
                onChange={handleChange}
                disabled={edit}
                inputProps={{ maxLength: 11 }}
              />
            </Col>
          </Row>
          <div className="flex flex-col mt-[5px]">
            {React.Children.toArray(
              editData?.ParentsInfos?.map((item) => (
                <AccountPopDetail
                  name={getFullname(item?.last_name, item?.first_name)}
                  phone={phoneHyphenRegex(item?.phone_number || '')}
                  relation={item?.UserAccount?.role}
                  username={item?.UserAccount?.username}
                />
              )),
            )}
          </div>
        </div>
      </Box>
      <Box title="태권도 정보">
        <div className="flex flex-col">
          <Row>
            <Col col={1}>
              <Label>태권도 경력</Label>
              <AdornmentInput
                unit="년"
                name="career"
                placeholder={!edit ? '태권도 경력 입력' : '0'}
                type="number"
                value={editData?.career || ''}
                onChange={handleChange}
                disabled={edit}
                onInput={numberMaxLength2}
              />
            </Col>
            <Col col={1}>
              <Label>급/품/단</Label>
              <Input
                name="level"
                type="text"
                value={editData?.level || ''}
                // onChange={handleChange}
                disabled
              />
            </Col>
          </Row>
          <Row>
            <Col col={1}>
              <Label>띠</Label>
              <RankImage name="belt" src={editData?.level || ''} />
            </Col>
          </Row>
        </div>
      </Box>
      <Box title="도장 정보">
        <div className="flex flex-col">
          <Row>
            <Col col={1}>
              <Label>도장 등록일</Label>
              <Input
                name="dojang_reg_date"
                value={editData?.dojang_reg_date || ''}
                // onChange={handleChange}
                disabled
              />
            </Col>
            <Col col={1}>
              <Label>수련생 등록일</Label>
              {edit ? (
                <Input
                  name="createdAt"
                  value={editData?.createdAt || ''}
                  onChange={handleChange}
                  disabled={edit}
                />
              ) : (
                <DatepickerSM
                  name="createdAt"
                  length={186}
                  value={editData?.createdAt || ''}
                  onChange={handleChange}
                />
              )}
            </Col>
          </Row>
        </div>
      </Box>
      <Box title="납부 정보">
        <div className="flex flex-col">
          <Row>
            <Col col={1}>
              <Label>납부일</Label>
              <AdornmentInput
                unit="일"
                name="due_date"
                value={editData?.due_date || ''}
                onChange={handleChange}
                disabled={edit}
                inputProps={{ maxLength: 2 }}
              />
            </Col>
            <Col col={1}>
              <Label>수강료</Label>
              <AdornmentInput
                unit="원"
                name="paying_amount"
                type="number"
                value={editData?.paying_amount || ''}
                onChange={handleChange}
                disabled={edit}
              />
            </Col>
          </Row>
          <Row>
            <Col col={1}>
              <Label>납부방법</Label>
              {edit ? (
                <Input
                  name="payment_method"
                  value={editData?.payment_method || ''}
                  onChange={handleChange}
                  disabled={edit}
                />
              ) : (
                <PayMethodSelect
                  name="payment_method"
                  defaultValue={editData?.payment_method || ''}
                  value={editData?.payment_method || ''}
                  onChange={handleChange}
                  width={186}
                  height={30}
                />
              )}
            </Col>
            <Col col={1}>
              <Label>입금자명</Label>
              <Input
                name="depositor_name"
                value={editData?.depositor_name || ''}
                onChange={handleChange}
                disabled={edit}
              />
            </Col>
          </Row>
          <Row>
            <Col className="pt-[10px]">
              <Label className="w-[88px] text-[#00196A] text-[14px] pb-[48px]">
                납부
                <br />
                참고사항
              </Label>
              <BasicTextArea
                rows={3}
                length={685}
                name="payment_note"
                value={editData?.payment_note || ''}
                onChange={handleChange}
                disabled={edit}
                className="rounded-[2px]"
              />
            </Col>
          </Row>
        </div>
      </Box>
      <Box title="줄넘기 정보">
        <div className="flex-col">
          <Row>
            <Col col={1}>
              <Label>줄넘기 경력</Label>
              <AdornmentInput
                unit="년"
                name="jump_career"
                type="number"
                value={editData?.jump_career || ''}
                onChange={handleChange}
                disabled={edit}
                onInput={numberMaxLength2}
              />
            </Col>
            <Col col={1}>
              <Label>줄넘기 급수</Label>
              <AdornmentInput
                unit="급"
                name="jump_level"
                type="number"
                value={editData?.jump_level || ''}
                onChange={handleChange}
                disabled={edit}
                onInput={numberMaxLength2}
              />
            </Col>
          </Row>
        </div>
      </Box>
      <Box title="메모">
        <div className="flex flex-col gap-2">
          <div className="flex">
            <Row>
              <Col col={1}>
                <Label className="w-[88px] text-[#00196A] text-[14px] pb-[48px]">
                  기타
                  <br />
                  참고사항
                </Label>
                <BasicTextArea
                  rows={3}
                  length={685}
                  name="memo"
                  value={editData?.memo || ''}
                  onChange={handleChange}
                  disabled={edit}
                />
              </Col>
            </Row>
          </div>
        </div>
      </Box>
    </>
  );
};

export default SpecInfo;
