import { useMutation } from '@tanstack/react-query';
import React, { useState } from 'react';
import { useRecoilState, useRecoilValue } from 'recoil';
import { createStudent } from '../../../../../../api/Student/studentApi';
import BlackBtnSM from '../../../../../../components/UI/Atoms/Button/BlackBtnSM';
import WhiteBtnSM from '../../../../../../components/UI/Atoms/Button/WhiteBtnSM';
import Stepper from '../../../../../../components/UI/Atoms/Stepper/Stepper';
import dojangIdAtom from '../../../../../../recoil/atom/dojangIdAtom';
import { getFormData } from '../../../../../../utils/basic/getFormData';
import { registDataAtom } from '../../recoil/atom/registDataAtom';
import StepA from './RegistStep/StepA';
import StepB from './RegistStep/StepB';
import StepC from './RegistStep/StepC';
import StepD from './RegistStep/StepD';
import StepE from './RegistStep/StepE';
import { nameRegex, lastNameRegex } from '../../../../../../utils/lib/Regex';
import { formDataDebug } from '../../../../../../utils/debug/debug';
import { toLevelString } from '../../../../../../utils/basic/transform';

const stepList = ['기본정보 1', '기본정보 2', '태권도 정보', '수납 정보', '기타 정보'];

const NewStudent = () => {
  const [step, setStep] = useState(0);

  // recoil
  const [reg, setReg] = useRecoilState(registDataAtom);
  const dojangId = useRecoilValue(dojangIdAtom);

  const [level, setLevel] = useState('');

  // query
  const { mutate, isLoading } = useMutation(([classId, body]) => createStudent([classId, body]));

  // handler
  const handlePrev = () => {
    setStep((currStep) => currStep - 1);
  };
  const handleNext = () => {
    setStep((currStep) => currStep + 1);
  };
  const handleSubmit = () => {
    const temp = { ...reg, dojang: dojangId };
    const { class_id, user, img, level, ...postData } = temp;
    const data = [...Object.entries(postData)];
    if (level) {
      data.push(['level', toLevelString(level)]);
    }
    if (img) {
      data.push(['img', img]);
    } else {
      alert('사진을 등록해주세요.');
    }

    const formData = getFormData(data);

    if (reg?.first_name === '') {
      alert('이름을 입력해주세요.');
      return;
    } else if (reg?.last_name === '') {
      alert('성을 입력해주세요.');
      return;
    } else if (!lastNameRegex.test(reg?.last_name)) {
      alert('성은 한글 1~3자로만 입력 가능합니다.');
      return;
    } else if (!nameRegex.test(reg?.first_name)) {
      alert('이름은 한글 1~6자로만 입력 가능합니다.');
      return;
    }

    if (reg?.sex === '') {
      alert('성별을 선택해주세요.');
      return;
    }
    if (reg?.dob === '') {
      alert('생년월일을 입력해주세요.');
      return;
    }
    if (reg?.class_id === '') {
      alert('반을 선택해주세요.');
      return;
    }
    if (reg?.level === '') {
      alert('급/품/단을 선택해주세요.');
      return;
    }
    if (reg?.dojang_reg_date === '') {
      alert('도장 등록일을 입력해주세요.');
      return;
    }

    // formDataDebug(formData);

    mutate([class_id, formData], {
      onSuccess: () => {
        alert('성공적으로 등록 되었습니다.');
        window.location.reload();
      },
      onError: () => alert('오류가 발생하였습니다.'),
    });
  };

  // useDebug(reg)

  return (
    <>
      <div className="mt-[70px]">
        <Stepper activeStep={step} steps={stepList} />
      </div>

      <div className="h-[500px] my-[20px]">
        {step === 0 && <StepA />}
        {step === 1 && <StepB />}
        {step === 2 && <StepC />}
        {step === 3 && <StepD />}
        {step === 4 && <StepE />}
      </div>

      <div className="flex gap-2 justify-center">
        {step > 0 && <WhiteBtnSM onClick={handlePrev}>이전</WhiteBtnSM>}
        {step < 4 && <BlackBtnSM onClick={handleNext}>다음</BlackBtnSM>}
        {step === 4 && <BlackBtnSM onClick={handleSubmit}>완료</BlackBtnSM>}
      </div>
    </>
  );
};

export default NewStudent;
