import React from 'react'
import Box from '../../../../../../components/layout/Box'
import { useRecoilValue } from 'recoil'
import { studentInfoAtom } from '../../recoil/atom/studentInfoAtom'
import { useDebug } from '../../../../../../hooks/basic/useDebug'
import ModalBtn from '../../../../../../components/UI/Atoms/Button/EditBtnSM'
import { useModal } from '../../../../../../hooks/basic/useModal'
import ClassPop from '../../../../../../components/common/Class/ClassPop'
import ClassListModal from '../../../../../../components/common/Class/ClassListModal'

const ClassInfo = () => {
  // recoil
  const student = useRecoilValue(studentInfoAtom)
  const { id, title } = student?.Classes[0]


  // custom hook
  const [ open, openFn, closeFn] = useModal()

  

  return (
    <>
      <Box
        title='반 정보'
        btn={
          <ModalBtn onClick={() => openFn()}>반변경</ModalBtn>
        }
      >
        <div className='flex'>
          <ClassPop class_id={id} class_name={title} />
        </div>
      </Box>

      <ClassListModal
        open={open}
        onClose={() => closeFn()}
      />
    </>
  )
}

export default ClassInfo