import React from 'react';
import SelectBox from '../../../../../../components/UI/Atoms/Select/SelectBox';

const TypeSelect = ({ title, children, src, ...props }) => {
  return (
    <SelectBox {...props}>
      <div className="text-[#555555] text-center">
        {title}
        <div
          style={{
            backgroundImage: `url(${src})`,
            backgroundPosition: 'center',
            backgroundRepeat: 'no-repeat',
            backgroundSize: 'contain',
            width: '195px',
            height: '200px',
            marginTop: '15px',
            marginBottom: '10px',
          }}
        ></div>
      </div>
      <div className="">{children}</div>
    </SelectBox>
  );
};

export default TypeSelect;
