import React, { useState, useEffect } from 'react';
import ModalContainer from '../../../../../components/layout/ModalContainer';
import StudentInfo from './StudentInfo';
import { useDebug } from '../../../../../hooks/basic/useDebug';
import ProfileImage from '../../../../../components/UI/Atoms/Profile/ProfileImage';
import { useSetRecoilState } from 'recoil';
import { studentInfoAtom } from '../recoil/atom/studentInfoAtom';
import Em from '../../../../../components/UI/Atoms/Ex/Em';
import { useStudentInfoFn } from '../hooks/queries/useStudentInfo';
import { phoneHyphenRegex } from '../../../../../utils/lib/Regex';
import { getStudent } from '../../../../../api/Student/studentApi';
import { toGender } from '../../../../../utils/render/toGender';
import { Grid } from '../../../../../components/common/Grid/Grid';
import Description from '../../../../../components/UI/Atoms/Ex/Description';
import { toAge } from '../../../../../utils/render/toAge';
import RankImage from '../../../../../components/common/Rank/RankImage';
import ArrowIcon from '../../../../../components/UI/Atoms/Icon/ArrowIcon';
import RankOption from '../../../../../components/common/Rank/RankOption';
import { getFullname } from '../../../../../utils/render/getFullname';

const StudentList = ({ res, refetch }) => {
  // count: 수련생 수, rows: 수련생 목록
  const { count, rows } = res;

  const [open, setOpen] = useState(false);

  // recoil : 수련생 정보
  const setStudentInfo = useSetRecoilState(studentInfoAtom);

  // query
  const student = useStudentInfoFn();

  // handler
  const handleClickItem = (e) => {
    const clickId = e.currentTarget.id;
    student.mutateAsync(clickId, {
      onSuccess: (res) => {
        setStudentInfo(res);
        setOpen(true);
      },
    });
  };

  const handleClose = () => {
    refetch();
    setOpen(false);
  };

  return (
    <>
      <Grid>
        <Grid.Head>
          <Description>
            현재 선택된 반의 수련생은 <Em>{count}명</Em>입니다.
          </Description>
        </Grid.Head>
        <Grid.RowWrap>
          {React.Children.toArray(
            rows?.map((item) => (
              <Grid.Row id={item?.id} onClick={handleClickItem}>
                <Grid.Col w={1} className="pr-0">
                  <ProfileImage urlKey={item?.photo_url} />
                </Grid.Col>
                <Grid.Col w={2}>{item?.last_name + item?.first_name}</Grid.Col>
                <Grid.Col w={1}>
                  <RankImage src={item?.level} />
                  {/* <RankOption /> */}
                </Grid.Col>
                <Grid.Col w={1}>{toGender(item?.sex)}</Grid.Col>
                <Grid.Col w={1}>{toAge(item?.dob)}</Grid.Col>
                <Grid.Col w={2}>{item?.Classes[0]?.title}</Grid.Col>
                <Grid.Col w={2}>{phoneHyphenRegex(item?.phone_number)}</Grid.Col>
                <Grid.Col>
                  <ArrowIcon />
                </Grid.Col>
              </Grid.Row>
            )),
          )}
        </Grid.RowWrap>
      </Grid>

      {/* modal */}
      <ModalContainer open={open} onClose={handleClose} title="수련생 정보">
        <StudentInfo />
      </ModalContainer>
    </>
  );
};

export default StudentList;
