import React from 'react'
import BasicInfo from './StudentInfo/BasicInfo'
import ClassInfo from './StudentInfo/ClassInfo'
import AccountInfo from './StudentInfo/AccountInfo'
import { useModal } from '../../../../../hooks/basic/useModal'
import ModalContainerAlert from '../../../../../components/layout/ModalContainerAlert'
import { useMutation } from '@tanstack/react-query'
import { outStudent, updateStudentSpecInfo } from '../../../../../api/Student/studentApi'
import { useRecoilValue } from 'recoil'
import { studentInfoAtom } from '../recoil/atom/studentInfoAtom'
import EditBtnSM from '../../../../../components/UI/Atoms/Button/EditBtnSM'
import AlertText from '../../../../../components/common/Alert/AlertText'
import { getFullname } from '../../../../../utils/render/getFullname'
import { useDebug } from '../../../../../hooks/basic/useDebug'
import { formDataDebug } from '../../../../../utils/debug/debug'
import dojangIdAtom from '../../../../../recoil/atom/dojangIdAtom'


const StudentInfo = () => {
  // recoil
  const student = useRecoilValue(studentInfoAtom)
  const dojangId = useRecoilValue(dojangIdAtom)
  
  // custom hook
  const [open, openFn, closeFn] = useModal()

  // query
  const { mutate: outMt, isLoading } = useMutation((studentId) => outStudent(dojangId, studentId))

  // handler
  const handleConfirm = () => {

    outMt(student?.id, {
      onSuccess: () => {
      alert('삭제 되었습니다.')
      window.location.replace('student')
    }})
  }

  
  return (
    <>
      <BasicInfo />
      <ClassInfo />
      <AccountInfo />
      <div className='flex justify-end'>
        <EditBtnSM onClick={openFn}>퇴소</EditBtnSM>
      </div>

      <ModalContainerAlert
        open={open}
        onClose={() => closeFn()}
        onConfirm={handleConfirm}
        confirmText='삭제'
        isLoading={isLoading}
      >
        <AlertText title>{`${getFullname(student?.last_name, student?.first_name)}`} 원생을 퇴소시킵니다.</AlertText>
        <AlertText>해당 원생의 정보들이 영구적으로 삭제됩니다.<br/>
        단, 원생의 태권정보는 유지됩니다.<br/>
        정말 퇴소시키겠습니까?</AlertText>
      </ModalContainerAlert>
    </>
  )
}

export default StudentInfo