import React, { useState } from 'react';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';

const selectItemStyle = {
  height: '30px',
  position: 'relative',
  display: 'flex',
  alignItems: 'center',
  '&:hover': {
    fontWeight: 'bold',
    backgroundColor: '#f3f3f3',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
  '&:focus': {
    fontWeight: 'bold',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
}

const GenderSelectSM = ({width, height, ...props}) => {
  const [gender, setGender] = useState(1);

  const handleChange = (e) => {
    setGender(e.target.value);
  };

  

  return (
    <>
        <Select
          sx={{ 
            width: '186px' && `${width}`, 
            height: '30px' && `${height}`, 
          }}
          value={gender}
          onChange={handleChange}
          {...props}
        >
          <MenuItem sx={selectItemStyle} value={1}>남자</MenuItem>
          <MenuItem sx={selectItemStyle} value={2}>여자</MenuItem>
        </Select>
    </>
  );
}

export default GenderSelectSM;