import React, { useState, useEffect } from 'react';
import BlackBtnSM from '../../../../../components/UI/Atoms/Button/BlackBtnSM';
import ExistStudent from './RegistStudent/ExistStudent';
import NewStudent from './RegistStudent/NewStudent';
import TypeSelect from './RegistStudent/TypeSelect';

const RegistStudent = () => {
  const [ step, setStep ] = useState('')
  const [ selectType, setSelectType ] = useState('')
  

  // handler
  const handleClickItem = (e) => {
    const clickId = e.currentTarget.id
      setSelectType(clickId)
  }

  const handleClick = () => {
    setStep(selectType);
  };

  const src = {
    new: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/student/registration/newstudent.png',
    existing:
      'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/student/registration/existingstudent.png',
  };

  return (
    <>
      {!step && (
        <>
          <div className="flex justify-center items-center gap-[10px]">
            <TypeSelect
              id="1"
              title="신규 수련생 등록"
              onClick={handleClickItem}
              className={selectType === '1' && 'type_selected'}
              src={src.new}
            >
              수련생을 신규 등록합니다.
            </TypeSelect>
            <TypeSelect
              id="2"
              title="기존 수련생 등록"
              onClick={handleClickItem}
              className={selectType === '2' && 'type_selected'}
              src={src.existing}
            >
              기존에 있는 수련생을 다시 등록합니다.
            </TypeSelect>
          </div>
          <div className="flex justify-center my-10">
            <BlackBtnSM disabled={!selectType} onClick={handleClick}>
              다음
            </BlackBtnSM>
          </div>
        </>
      )}
      {step === '1' && <NewStudent />}
      {step === '2' && <ExistStudent />}
    </>
  );
};

export default RegistStudent;
