import React from 'react'
import { Box as MUIBox } from '@mui/material';


const ExistBox = ({ bg, children, ...props }) => {
  return (
    <>
      <MUIBox sx={{
        bgcolor: bg ? bg: '#ffffff',
        color: '#FFFFFF', 
        borderRadius: 0,
        mt: 1,
        mb: 1,
        pt: 2,
        px: '20px',
        pb: 2,
      }} {...props}>
        {children}
      </MUIBox>
    </>
  )
}

export default ExistBox