import React from 'react'
import { styled } from '@mui/material/styles'
import { Box } from '@mui/material'
import ProfileImage from '../../Atoms/Profile/ProfileImage'

const AccountPopDetailWrap = styled(Box)(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  gap: '10px',
  margin: '2px 0',
  border: '1px solid #dddddd',
  borderRadius: '5px',
  padding: '0 20px',
  backgroundColor: '#F2F2F2',
  width: 'auto',
  height: '50px',
  letterSpacing: 'normal',
  fontSize: '15px',
  '&:hover': {
    backgroundColor: theme.palette.background.hover,
  },
  '& > div': {
    marginRight: '10px',
  },
}))

const AccountPopDetail = ( { userName, name, relation, phone }) => {
  return (
    <>
      <AccountPopDetailWrap>
        <div>
          <ProfileImage size='30px' />
        </div>
        <div className='w-[140px]'>
          {name}
        </div>
        <div className='w-[60px]'>
          {relation || '가족'}
        </div>
        <div className='w-[120px]'>
          {phone}
        </div>
        <div className=''>
          ID : {userName || '없음'}
        </div>
      </AccountPopDetailWrap>
    </>
  )
}

export default AccountPopDetail