import React from 'react';
import { useRecoilState } from 'recoil';
import ModalAddress from '../../../../../../../components/common/PostCode/ModalAddress';
import Box from '../../../../../../../components/layout/Box';
import InfoLabelSM from '../../../../../../../components/UI/Atoms/Info/InfoLabelSM';
import InfoEditInput from '../../../../../../../components/UI/Atoms/Input/InfoEditInput';
import { useModal } from '../../../../../../../hooks/basic/useModal';
import { registDataAtom } from '../../../recoil/atom/registDataAtom';
import RegInfo from './RegInfo';
import { numberMaxLength, numberMaxLength1 } from '../../../../../../../utils/render/numberMaxLength';
import AdornmentInput from '../../../../../../../components/UI/Atoms/Input/AdornmentInput';

const StepB = () => {
  const [reg, setReg] = useRecoilState(registDataAtom);

  // custom hook
  const [open, oepnFn, closeFn] = useModal();

  // handler
  const handleChange = (e) => {
    const { name, value } = e.target;
    setReg({ ...reg, [name]: value });
  };

  return (
    <>
      <RegInfo />
      <Box>
        <div className="flex flex-col gap-2 justify-center px-[70px]">
          <div className="flex gap-[40px]">
            <div className="flex flex-1 items-center">
              <InfoLabelSM>체중</InfoLabelSM>
              <AdornmentInput
                unit="kg"
                placeholder="체중을 입력하세요."
                type='number'
                name="weight"
                className="rounded-[5px]"
                value={reg?.weight || ''}
                onChange={handleChange}
                onInput={numberMaxLength}
              />
            </div>
            <div className="flex flex-1 items-center">
              <InfoLabelSM>키</InfoLabelSM>
              <AdornmentInput
                unit="cm"
                placeholder="키를 입력하세요."
                type='number'
                name="height"
                className="rounded-[5px]"
                value={reg?.height || ''}
                onChange={handleChange}
                onInput={numberMaxLength}
              />
            </div>
          </div>
          <div className="flex gap-[40px]">
            <div className="flex flex-1 items-center">
              <InfoLabelSM>전화번호</InfoLabelSM>
              <InfoEditInput
                type='tel'
                placeholder="'-'없이 입력해 주세요."
                name="phone_number"
                value={reg?.phone_number || ''}
                onChange={handleChange}
                maxLength={11}
              />
            </div>
            <div className="flex flex-1 items-center">
              <InfoLabelSM>보호자 이름</InfoLabelSM>
              <InfoEditInput
                placeholder="보호자 이름"
                name="Rep_name"
                value={reg?.Rep_name || ''}
                onChange={handleChange}
                inputProps={{ maxLength: 9 }}
              />
            </div>
          </div>
          <div className="flex gap-[40px]">
            <div className="flex flex-1 items-center">
              <InfoLabelSM>이메일</InfoLabelSM>
              <InfoEditInput
                type="email"
                placeholder="이메일 입력"
                name="email"
                value={reg?.email || ''}
                onChange={handleChange}
              />
            </div>
            <div className="flex flex-1 items-center">
              <InfoLabelSM>보호자 연락처</InfoLabelSM>
              <InfoEditInput
                type='tel'
                placeholder="'-'없이 입력해 주세요."
                name="Rep_phone_number"
                value={reg?.Rep_phone_number || ''}
                onChange={handleChange}
                maxLength={11}
              />
            </div>
          </div>
          <div className="flex flex-col gap-[10px] mb-[20px]">
            <div className="flex flex-1 items-center">
              <InfoLabelSM length={100}>주소</InfoLabelSM>
              <InfoEditInput
                placeholder="클릭하여 주소를 검색해 주세요."
                length={348}
                name="address_name"
                value={reg?.address_name || ''}
                onChange={handleChange}
                onClick={oepnFn}
                inputProps={{ readOnly: true}}
              />
              <InfoEditInput
                placeholder="상세 주소 입력"
                length={186}
                name="address_detail"
                value={reg?.address_detail || ''}
                onChange={handleChange}
                className="ml-[2px]"
              />
            </div>
            <ModalAddress open={open} onClose={closeFn} setValue={setReg} />
            <div className="flex flex-1 items-center">
              <InfoLabelSM length={100}>학교</InfoLabelSM>
              <InfoEditInput
                placeholder="학교 입력"
                length={348}
                name="school"
                value={reg?.school || ''}
                onChange={handleChange}
              />
              <AdornmentInput
                unit="학년"
                placeholder="학년 입력"
                type="number"
                name="school_detail"
                value={reg?.school_detail || ''}
                onChange={handleChange}
                className="ml-[2px] rounded-[5px]"
                onInput={numberMaxLength1}
              />
            </div>
          </div>
        </div>
      </Box>
    </>
  );
};

export default StepB;
