import { useMutation } from "@tanstack/react-query"
import { useRecoilValue } from "recoil"
import { updateStudentClass } from "../../api/Student/studentApi"
import { studentInfoAtom } from "../../pages/UserDojang/Manage/Student/recoil/atom/studentInfoAtom"

export const useStudentClassChangeFn = () => {
  const { id } = useRecoilValue(studentInfoAtom)
  
  return useMutation((classId) => updateStudentClass(id, classId), {
  })
}
