import React, { useState } from 'react';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import theme from '../../../../themes';
import MenuItem from '@mui/material/MenuItem';

const SelectStyle = {
  '& .MuiSelect-select': {
    padding: '2.5px 10px',
    border: `1px solid ${theme.select.basic.border}`,
    borderRadius: '5px',
    fontSize: '12px',
    fontWeight: '400',
    '&:hover': {
      borderColor: theme.select.basic.hover.border,
    },
    '&.Mui-focused': {
      border: `1px solid ${theme.select.basic.focus.border}`,
    },
    '&.Mui-disabled': {
      borderColor: theme.select.basic.disabled.border,
    },
  },
};

const optionStyle = {
  fontSize: '12px',
  fontWeight: '400',
};

const PayMethodSelect = ({ ...props }) => {
  
  const [value, setValue] = useState(props.defaultValue);

  const handleChange = (e) => {
    setValue(e.target.value);
  };

  const MenuItems = [
    { value: '현금' },
    { value: '카드' },
    { value: '계좌이체' },
  ];

  return (
    <>
      <FormControl
        sx={{
          width: props.width,
          height: props.height,
        }}
      >
        <Select
          defaultValue={props.defaultValue}
          value={value}
          onChange={handleChange}
          sx={SelectStyle}
          {...props}
        >
          {React.Children.toArray(
            MenuItems.map((item) => (
              <MenuItem sx={optionStyle} value={item.value}>
                {item.value}
              </MenuItem>
            )),
          )}
        </Select>
      </FormControl>
    </>
  );
};

export default PayMethodSelect;
