import React from 'react'
import { useRecoilState, useRecoilValue } from 'recoil';
import classListAtom from '../../../recoil/atom/classListAtom';
import classCntAtom from '../../../recoil/atom/classCntAtom';
import dojangInfoAtom from '../../../recoil/atom/dojangInfoAtom';
import { studentInfoAtom } from '../../../pages/UserDojang/Manage/Student/recoil/atom/studentInfoAtom';
import { useStudentInfoFn } from '../../../pages/UserDojang/Manage/Student/hooks/queries/useStudentInfo';
import { useStudentClassChangeFn } from '../../../hooks/queries/useStudentClassChange';
import EditBtnSM from '../../UI/Atoms/Button/EditBtnSM';
import ModalContainerSM from '../../layout/ModalContainerSM';
import CloseIcons from '../../UI/Atoms/Icon/CloseIcons';
import { useDebug } from '../../../hooks/basic/useDebug';
import { useQuery } from '@tanstack/react-query';
import { getClassList } from '../../../api/Class/ClassApi';


const ClassListModal = ({ open, onClose }) => {
  // recoil
  const dojangInfo = useRecoilValue(dojangInfoAtom);
  const [studentInfo, setStudentInfo] = useRecoilState(studentInfoAtom);
  // query
  const { data: classList } = useQuery(['getClassList', dojangInfo?.id], () => getClassList(dojangInfo?.id), {
    enabled: !!dojangInfo?.id,
    select: (res) => {
      return res.rows
    }

  })
  const update = useStudentClassChangeFn();
  const refetch = useStudentInfoFn();

  // handler
  const handleClick = async (e) => {
    const classId = e.target.id;

    await update.mutateAsync(classId, {
      onSuccess: () => {
        refetch.mutateAsync(studentInfo?.id, {
          onSuccess: (res) => {
            setStudentInfo(res);
            onClose();
          },
        });
      },
    });
  };

  return (
    <>
      <ModalContainerSM
        open={open}
        onClose={onClose}
        title={
          <>
            <strong>[{dojangInfo?.name}]</strong>에는 <br/>
            현재 {classList?.length}개 반이 있습니다.
          </>
        }
      >
        <div className='flex flex-col justify-center pt-3'>
          { classList && 
            React.Children.toArray(
            classList?.map( item => 
              <div className='flex justify-between my-1 px-4 py-2 items-center bg-[#F5F7F8] h-[50px] rounded-[5px]'>
                {item?.title}
                <EditBtnSM id={item?.id} onClick={handleClick}>선택</EditBtnSM>
              </div>
            )
          )}
        </div>

      </ModalContainerSM>
    </>
  )
}

export default ClassListModal;
