import React from 'react'
import Box from '../../../../../../components/layout/Box'
import { useRecoilValue } from 'recoil'
import { studentInfoAtom } from '../../recoil/atom/studentInfoAtom'
import AccountPop from '../../../../../../components/UI/Molecules/Account/AccountPop'
import { useDebug } from '../../../../../../hooks/basic/useDebug'
import ModalBtn from '../../../../../../components/UI/Atoms/Button/EditBtnSM'

const AccountInfo = () => {
  const student = useRecoilValue(studentInfoAtom)
  const accountList = student?.ParentsInfos
  // useDebug(accountList)

  return (
    <Box
      title='연결 계정 정보'
      >
      <div className='flex flex-col gap-[6px] my-[10px]'>
        { React.Children.toArray(
          accountList?.map((item) =>
            <>
              <AccountPop
                name={item?.last_name + item?.first_name}
                relation={item?.UserAccount?.role} 
                userName={item?.UserAccount?.username}
              />
            </>
          )
        )}
      </div>
    </Box>
  )
}

export default AccountInfo