import React from 'react';
import { useRecoilState } from 'recoil';
import Box from '../../../../../../../components/layout/Box';
import InfoLabelSM from '../../../../../../../components/UI/Atoms/Info/InfoLabelSM';
import AdornmentInput from '../../../../../../../components/UI/Atoms/Input/AdornmentInput';
import InfoEditInput from '../../../../../../../components/UI/Atoms/Input/InfoEditInput';
import RankSelect from '../../../../../../../components/UI/Molecules/Rank/RankSelect';
import { toLevelString } from '../../../../../../../utils/basic/transform';
import { numberMaxLength2 } from '../../../../../../../utils/render/numberMaxLength';
import { registDataAtom } from '../../../recoil/atom/registDataAtom';
import RegInfo from './RegInfo';

const StepC = () => {
  const [reg, setReg] = useRecoilState(registDataAtom);

  // handler
  const handleChange = (e) => {
    const { name, value } = e.target;
    setReg({ ...reg, [name]: value });
  };

  return (
    <>
      <RegInfo />
      <Box>
        <div className="flex flex-col gap-[20px] px-[70px]">
          <div className="flex items-center gap-[40px]">
            <div className="flex flex-1 items-center">
              <InfoLabelSM>태권도 경력</InfoLabelSM>
              <AdornmentInput
                unit="년"
                placeholder="0"
                type="number"
                name="career"
                value={reg?.career}
                onChange={handleChange}
                onInput={numberMaxLength2}
              />
            </div>
            <div className="flex flex-1 items-center">
              <InfoLabelSM>급/품/단*</InfoLabelSM>
              <RankSelect 
                placeholder="급/품/단 선택"
                value={reg?.level}
                name="level" 
                onChange={handleChange} 
                className='h-[30px] w-[186px]'
                sx={{
                  '& . MuiSelect-select': {
                    border: '0 none',
                  },
                }}
              />
            </div>
          </div>
          <div className="flex flex-1 items-center mb-[20px]">
            <InfoLabelSM>품(단)증 번호</InfoLabelSM>
            <InfoEditInput
              type="number"
              placeholder="품(단)증 번호 입력"
              length={536}
              name="level_number"
              value={reg?.level_number}
              onChange={handleChange}
            />
          </div>
        </div>
      </Box>
    </>
  );
};

export default StepC;
