import React from 'react'
import MenuItem from '@mui/material/MenuItem';
import { Select } from '@mui/material';

const selectItemStyle = {
  height: '30px',
  position: 'relative',
  display: 'flex',
  alignItems: 'center',
  '&:hover': {
    fontWeight: 'bold',
    backgroundColor: '#f3f3f3',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
  '&:focus': {
    fontWeight: 'bold',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
}

const GenderSelect = ({value, onChange, placeholder, ...props}) => {
  // gender data
  const genderData = [
    { id: 1, title: '남자' },
    { id: 2, title: '여자' },
  ]

  const handleChange = (e) => {
    // const val = e.target.value === 1 ? 'true' : 'false'
    const event = {target: { name: props.name, value: e.target.value}}
    onChange(event)
  }

  return (
    <>
      <Select 
        sx={{width: '186px', height: '30px'}}
        size='small'
        placeholder={placeholder}
        value={value}
        onChange={handleChange}
        {...props}
      >
        {React.Children.toArray(
          genderData?.map(({id, title}) =>
            <MenuItem value={id} sx={selectItemStyle}
            >
              {title}
            </MenuItem>
          )
        )}
      </Select>
    </>
  )
}

export default GenderSelect