import React, { useState, useEffect } from 'react'
import Box from '../../../../../../components/layout/Box'
import { useRecoilState, useRecoilValue } from 'recoil'
import { studentInfoAtom } from '../../recoil/atom/studentInfoAtom'
import ModalContainer from '../../../../../../components/layout/ModalContainer'
import SpecInfo from './SpecInfo'
import Name from '../../../../../../components/UI/Atoms/Ex/Name'
import ProfileImage from '../../../../../../components/UI/Atoms/Profile/ProfileImage'
import { useDebug } from '../../../../../../hooks/basic/useDebug'
import ModalBtn from '../../../../../../components/UI/Atoms/Button/EditBtnSM'
import InfoLabelSM from '../../../../../../components/UI/Atoms/Info/InfoLabelSM'
import InfoData from '../../../../../../components/UI/Atoms/Info/InfoData'
import { useStudentSpecInfo } from '../../hooks/queries/useStudentSpecInfo'
import { useMutation } from '@tanstack/react-query'
import { getStudentInfo, getStudentSpecInfo } from '../../../../../../api/Student/studentApi'
import { useStudentInfoFn } from '../../hooks/queries/useStudentInfo'
import { phoneHyphenRegex } from '../../../../../../utils/lib/Regex'

const BasicInfo = () => {
  const [ student, setStudent ] = useRecoilState(studentInfoAtom)
  const [open, setOpen] = useState(false)
  const [specInfo, setSpecInfo] = useState('')

  // query
  const { mutate } = useMutation(() => getStudentSpecInfo(student?.id))
  const { mutate: infoMt } = useStudentInfoFn()

  // handler
  const handleOpen = e => {
    mutate(student?.id, {
      onSuccess: (res) => {
        setSpecInfo(res)
        setOpen(true)
      }
    })
  }

  const handleClose = e => {
    infoMt(student?.id, {
      onSuccess: (res) => {
        setStudent(res)
        setOpen(false)
      }
    })
  }

  return (
    <>
      <Box
        title='기본 정보'
        btn={
          <ModalBtn onClick={handleOpen}>상세정보</ModalBtn>
        }
      >
        <div className="flex">
          <div className='basis-1/5'>
            <ProfileImage 
              size='140px'
              urlKey={student?.photo_url}
            />
          </div>
          <div className='flex flex-col basis-4/5 justify-center'>
            <Name>{student?.last_name + student?.first_name}</Name>
            <div className='flex items-center'>
              <InfoLabelSM>생년월일</InfoLabelSM>
              <InfoData>{student?.dob}</InfoData>
              <InfoLabelSM>보호자</InfoLabelSM>
              <InfoData>{student?.Rep_name} {student?.Rep_rel}</InfoData>
            </div>
            <div className='flex items-center'>
              <InfoLabelSM>전화번호</InfoLabelSM>
              <InfoData>{phoneHyphenRegex(student?.phone_number)}</InfoData>
              <InfoLabelSM>보호자 연락처</InfoLabelSM>
              <InfoData>{phoneHyphenRegex(student?.Rep_phone_number)}</InfoData>
            </div>
          </div>
        </div>
      </Box>

      <ModalContainer
        open={open}
        onClose={handleClose}
        title='수련생 상세정보'
      >
        <SpecInfo info={specInfo} setInfo={setSpecInfo} />
      </ModalContainer>
    </>
  )
}

export default BasicInfo